@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');

*{
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
body{
  background-color: rgb(244, 244, 244);
  /* background-color: white; */
}

h4{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 24px;
}

.borderbottom{
  margin-top: 10px;
  border-bottom: 1px solid rgb(228, 228, 228);
}

nav{
  position: sticky;
  top: 0;
  background: black;
  height: 70px;
  width: 100%;
  /* border-bottom: 2px solid white; */
  border-bottom: 1px solid #dbdbdb;
  backdrop-filter: blur(5px);
  background-color: rgba(255, 255, 255, 0.473);
  z-index: 100;
  line-height: 60px;
}
label.logo{
  color: white;
  font-size: 33px;
  line-height: 70px;
  margin-left: 10%;
  font-weight: bold;
  font-family: 'Oleo Script', cursive;
}

nav img{
  line-height: 70px;
  padding-bottom: 3px;
}

label.logo a{
  color: white;
  text-decoration: none;
}

label.logo a img{
  height: 70px;
}

label.logo a:hover{
  background-color: transparent;
}

nav ul{
  display: inline-block;
}

.link-class {
  padding: 0px;
  margin: 0px;
}

nav .contactbtn{
  float: right;
  margin-right: 10%;
}

nav .contactbtn a{
  color: white;
}

nav .contactbtn button{
  background-color: black;
  border: 2px solid black;
  color: white;
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  font-size: 15px;
  border-radius: 5px;
  padding: 7px;
  margin-top: 15px;
  padding-left: 20px;
  padding-right: 20px;
}

nav .contactbtn button:hover{
  cursor: pointer;
}

nav ul li{
  float: left;
  line-height: 70px;
  margin: 0 5px;
}
nav ul li a{
  color: black;
  font-size: 15.2px;
  padding: 7px 13px;
  margin-left: 20px;
  border-radius: 0px;
  font-family: 'Poppins', sans-serif;
}

nav ul li a:hover{
  color: #0a7aeb;
}

