nav ul li a:hover:after{
  width: 100%;
}

a.active{
  border-bottom: 3px solid white;
  transition: .1s;
}

.active{
  border-bottom: 3px solid white;
  transition: .1s;
}

.checkbtn{
  
  color: white;
  float: right;
  margin-top: 5px;
  line-height: 70px;
  margin-right: 10%;
  cursor: pointer;
  display: none;
}

@media (max-width: 952px){
  label.logo{
    font-size: 30px;
  }
  nav ul li a{
    font-size: 16px;
  }
}
@media (max-width: 1030px){
  .checkbtn{
    display: block;
  }
  ul{
    position: fixed;
    width: 100%;
    height: 100vh;
    background: rgb(244, 244, 244);
    top: 70px;
    left: -100%;
    text-align: center;
    transition: all .5s;
    display: none;
  }
  nav ul li{
    display: block;
    line-height: 30px;
    margin-top: 70px;
    margin-left: 0px;
  }
  nav ul li a{
    font-size: 18px;
    margin-left: 0px;
    color: #0e3a5c;
  }

  /*#check:checked ~ ul{
    left: 0;
  }*/
}

.menu{
  display: none;
  background-color: #0e3a5c;
  width: 100%;
  padding-top: 30px;
  padding-bottom: 30px;
  border-bottom: 0px solid #06243b;
  z-index: 1;
  transition: all .5s;
  box-shadow: 2px 5px 5px grey;
}

.menu a{
  display: block;
  color: white;
  font-weight: bold;
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
  margin-top: 20px;
  padding-top: 20px;
  font-family: Arial, Helvetica, sans-serif;
  border-top: .5px solid white;

}

.plues{
  float: right;
  position: relative;
  z-index: 1;
}

.homeimage{
  width: 100%;
  background-color: black;
  margin-top: 20px;
  padding-top: 80px;
  margin-bottom: 20px;
}

.homeimage h3{
  color: white;
  font-size: 40px;
  line-height: 1;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.homeimage a{
  color: #0a7aeb;
  vertical-align: middle;
  font-family: Arial, Helvetica, sans-serif;
}

.homeimage a img{
  vertical-align: middle;
}

.homeimage p{
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
}

#currentpr{
  margin-bottom: 40px;
}

/* product */

.product-holder{
  gap: 20px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.product{
  padding: 25px;
  border-radius: 10px;
  background-color: white;
}

.product h3{
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 0px;
}

.product h5{
  font-family: 'Roboto', sans-serif;
  color: rgb(124, 124, 124);
  font-weight: bold;
  font-size: 17px;
}

.product p{
  color: gray;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 18px;
}

.product button{
  margin-top: 15px;
  margin-bottom: 0px;
  background-color: black;
  border: none;
  color: white;
  padding: 10px;
  font-size: 15px;
  font-weight: bold;
  border-radius: 30px;
  padding-left: 20px;
  padding-right: 20px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.product button:hover{
  transition: .2s;
  background-color: rgba(0, 0, 0, 0.829);
  cursor: pointer;
}

/* delifery */

.delifery-holder{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  background-color: white;
  border-radius: 10px;
}

.delifery{
  padding: 20px;
  
}

.delifery h5{
  font-family: 'Roboto', sans-serif;
  margin-top: 10px;
  font-size: 16px;
}

main{
  margin-left: 10%;
  margin-right: 10%;
}

footer{
  background-color: white;
  border-top: 1px solid #dbdbdb;
}

.footer-holder{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

