  main{

  }
}



@media(min-width: 1800px){
  label.logo{
    margin-left: 15%;

  }

  main{
    margin-left: 15%;
    margin-right: 15%;
  }

  nav .contactbtn{
    margin-right: 15%;
  }
  
}
